arell', sans-serif;\r\n    line-height: 1.6;\r\n    color: #1f2937;\r\n    background: linear-gradient(135deg, #f9fafb 0%, #f3f4f6 100%);\r\n    min-height: 100vh;\r\n    -webkit-font-smoothing: antialiased;\r\n    -moz-osx-font-smoothing: grayscale;\r\n    overflow-x: hidden;\r\n}\r\n\r\n/* CSS Custom Properties (Design Tokens) */\r\n:root {\r\n    /* Colors */\r\n    --primary-50: #eef2ff;\r\n    --primary-100: #e0e7ff;\r\n    --primary-500: #6366f1;\r\n    --primary-600: #4f46e5;\r\n    --primary-700: #4338ca;\r\n    --primary-900: #312e81;\r\n\r\n    --gray-50: #f9fafb;\r\n    --gray-100: #f3f4f6;\r\n    --gray-200: #e5e7eb;\r\n    --gray-300: #d1d5db;\r\n    --gray-400: #9ca3af;\r\n    --gray-500: #6b7280;\r\n    --gray-600: #4b5563;\r\n    --gray-700: #374151;\r\n    --gray-800: #1f2937;\r\n    --gray-900: #111827;\r\n\r\n    --success-50: #f0fdf4;\r\n    --success-500: #22c55e;\r\n    --success-600: #16a34a;\r\n\r\n    --warning-50: #fffbeb;\r\n    --warning-500: #f59e0b;\r\n    --warning-600: #d97706;\r\n\r\n    --error-50: #fef2f2;\r\n    --error-500: #ef4444;\r\n    --error-600: #dc2626;\r\n\r\n    /* Spacing */\r\n    --space-1: 0.25rem;\r\n    --space-2: 0.5rem;\r\n    --space-3: 0.75rem;\r\n    --space-4: 1rem;\r\n    --space-5: 1.25rem;\r\n    --space-6: 1.5rem;\r\n    --space-8: 2rem;\r\n    --space-10: 2.5rem;\r\n    --space-12: 3rem;\r\n    --space-16: 4rem;\r\n    --space-20: 5rem;\r\n\r\n    /* Border Radius */\r\n    --radius-sm: 0.25rem;\r\n    --radius-md: 0.5rem;\r\n    --radius-lg: 0.75rem;\r\n    --radius-xl: 1rem;\r\n    --radius-2xl: 1.5rem;\r\n    --radius-full: 9999px;\r\n\r\n    /* Shadows */\r\n    --shadow-sm: 0 1px 2px 0 rgb(0 0 0 / 0.05);\r\n    --shadow-md: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);\r\n    --shadow-lg: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);\r\n    --shadow-xl: 0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1);\r\n\r\n    /* Transitions */\r\n    